.experience__details,
.experience__input {
    display:none;
}

.experience__input:checked + .experience__title + .experience__details {
    display: inherit;
}

.experience__header {
    color: $color-periwinkle-gray;
    @extend %header;
    padding-top: 3rem;
}

.experience__title {
    background-color:$color-gumbo;
    color: $color-white;
    display: block;
    font-size:1.8rem;
    padding: 2rem;
    cursor: pointer;
}

.experience__details {
    border:.2rem solid $color-gumbo;
}

.experience__top {
    display: flex;
    justify-content: space-between;
}

.experience__date {
    font-style:italic;
}

.experience__company {
    font-weight:bold;
}

.experience__company,
.experience__date {
    font-size: 1.3rem;
    padding: 1rem;
}

.experience__responsibility {
padding: .5rem 1rem;
font-size:1.2rem;
line-height: 1.2;
    &:nth-child(odd) {
        background-color: $color-fantasy;
    }
    &:nth-child(even) {
        background-color: $color-twilight;
    }
}

.my-cv__icon--plus {
    float:right;
    pointer-events: none;
}

 .experience__input:checked + .experience__title {
    .my-cv__icon--plus {
        transform:rotateZ(45deg);
        pointer-events: all;
        cursor: pointer;
    }
}