.about-me {
      background-image: radial-gradient(
          ellipse,
          $color-geraldine,
          $color-cardinal
          );
}
    
    .about-me__header {
      color: $color-white;
      @extend %header;
    }
    
    .about-me__full-name {
      font-size: 2.5rem;
    }
    
    .about-me__title,
    .portfolio-title {
      font-size: 2rem;
    }
    
    .about-me__full-name,
    .about-me__title,
    .portfolio-title {
      text-align: center;
      padding: 2rem 1rem;
      color: $color-white;
    }
    
    p {
      font-size: 1.2rem;
      line-height: 1.7;
      text-align: justify;
      padding: .7rem 2rem;
      text-indent: 2rem;
    }
    
    .about-me__link-list,
    .portfolio__link {
      text-align: center;
    }
    
    .about-me__link {
      border: .1rem solid $color-lily;
      color: $color-gumbo;
      background-color: $color-white;
      padding: 1rem;
      width: 75%;
      margin: 1rem auto;
      display: inline-flex;
      justify-content: space-between;
      align-items: center;
      font-size: 3rem;
      border-radius: .3rem;
      text-decoration: none;
    }
    
    .about-me__link-description,
    .portfolio__link-description {
      text-align: center;
      font-size: 1.5rem;
      line-height: 1.5;
    }
    
    .about-me__avatar-wrapper {
      max-width: 50%;
      margin: 1rem auto;
      border: .7rem solid rgba(178, 30, 50,.3);
      position: relative;
      border-radius: 50%;
    
      &::after {
        position: absolute;
        content: '';
        left: -1.5rem;
        top: -1.5rem;
        bottom: -1.5rem;
        right: -1.5rem;
        border-radius: 50%;
        border: 1.5rem solid rgba(178, 30, 50,.3);
      }
    }
    
    .about-me__avatar {
      border-radius: 50%;
      width: 100%;
      border: .5rem solid $color-cardinal;
    } 

    .about-me__slogan,
    .portfolio-description {
      color:$color-tarawera;
      max-width: 70%;
      margin: 0 auto;
      text-align: center;
    }